import React from "react"
import { Link } from "gatsby"
import Layout from "../components/Layout"
import styled from "styled-components"
import { motion } from "framer-motion"
import {
  Favorite,
  People,
  Healing,
  PlayCircleFilled,
} from "@mui/icons-material"

import Seo from "../components/Seo"

import backgroundImage from "../images/psychologist.jpg"

const Container = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
`

const Hero = styled.div`
  position: relative;
  overflow: hidden;
  text-align: center;
  padding: 100px 20px;
  background: linear-gradient(rgba(108, 92, 231, 0.8), rgba(108, 92, 231, 0.6)),
    url(${backgroundImage}) top/cover no-repeat;
  color: white;
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  h1 {
    font-size: clamp(2.5rem, 5vw, 4rem);
    text-transform: uppercase;
    margin-bottom: 20px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  }

  p {
    font-size: clamp(1.5rem, 3vw, 2.5rem);
    max-width: 800px;
    margin: 0 auto;
    line-height: 1.4;
  }
`

const ServicesSection = styled(motion.section)`
  padding: 80px 0;
  background: #f8f9fa;
  text-align: center;
`

const ServiceGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-top: 50px;
`

const ServiceCard = styled(motion.div)`
  background: white;
  padding: 40px 30px;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
  text-align: center;
  transition: all 0.3s ease;
  transform-origin: center;
  will-change: transform, box-shadow;

  svg {
    font-size: 50px;
    color: #6c5ce7;
    margin-bottom: 20px;
    transition: all 0.3s ease;
  }

  h3 {
    color: #31314d;
    margin-bottom: 15px;
    font-size: 22px;
    transition: all 0.3s ease;
  }

  p {
    color: #666;
    line-height: 1.6;
    transition: all 0.3s ease;
  }

  &:hover {
    transform: translateY(-10px) scale(1.03);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);

    svg {
      color: #00cec9;
      transform: scale(1.1);
    }

    h3 {
      color: #6c5ce7;
    }

    p {
      color: #444;
    }
  }
`

const CTAButton = styled(motion(Link))`
  background: #00cec9;
  color: white;
  border: none;
  padding: 15px 40px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 50px;
  margin-top: 50px;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  box-shadow: 0 4px 15px rgba(0, 206, 201, 0.3);
  text-decoration: none;

  svg {
    font-size: 24px;
  }
`

const SectionTitle = styled(motion.h2)`
  color: #31314d;
  font-size: 36px;
  margin-bottom: 20px;
`

const SectionSubtitle = styled(motion.p)`
  color: #666;
  font-size: 18px;
  max-width: 700px;
  margin: 0 auto 40px;
`

const cardVariants = {
  offscreen: {
    y: 50,
    opacity: 0,
  },
  onscreen: {
    y: 0,
    opacity: 1,
    transition: {
      type: "spring",
      bounce: 0.4,
      duration: 0.8,
    },
  },
}

export default function Home() {
  return (
    <Layout>
      <Seo
        title="Главная страница"
        description="Психологические тесты для самопознания"
      />
      <Hero>
        <h1>All you need is love</h1>
        <p>Психодиагностика в отношениях — ключ к гармонии...</p>
      </Hero>

      <ServicesSection
        initial="offscreen"
        whileInView="onscreen"
        viewport={{ once: true, amount: 0.2 }}
      >
        <Container>
          <SectionTitle
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.2 }}
          >
            Путь к гармоничным отношениям
          </SectionTitle>
          <SectionSubtitle
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.4 }}
          >
            Мы поможем вам на каждом этапе ваших отношений
          </SectionSubtitle>

          <ServiceGrid>
            <ServiceCard variants={cardVariants}>
              <Favorite fontSize="large" />
              <h3>Поиск отношений</h3>
              <p>
                Узнайте больше о себе и своих предпочтениях, чтобы найти
                подходящего партнера.
              </p>
            </ServiceCard>

            <ServiceCard variants={cardVariants}>
              <People fontSize="large" />
              <h3>Улучшение отношений</h3>
              <p>
                Пройдите тестирование, чтобы выявить сильные стороны и зоны
                роста.
              </p>
            </ServiceCard>

            <ServiceCard variants={cardVariants}>
              <Healing fontSize="large" />
              <h3>Развод или расставание</h3>
              <p>
                Получите поддержку и советы, как справиться с эмоциональной
                болью и начать новую жизнь.
              </p>
            </ServiceCard>
          </ServiceGrid>

          <motion.p
            style={{ marginTop: "40px", fontSize: "20px" }}
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.6 }}
          >
            Начните путь к гармонии уже сегодня — первый шаг к изменениям
            зависит от вас.
          </motion.p>

          <CTAButton
            to="/status"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            Начать <PlayCircleFilled />
          </CTAButton>
        </Container>
      </ServicesSection>
    </Layout>
  )
}
